import skyRaty from "../../components/skyraty.vue";
export default{
	components:{
		skyRaty
	},
	data:function(){
		return {
			pageData:{},
			pageLoad:false,
			orderid:0
		}
	},
	onLoad:function(ops){
		uni.setNavigationBarTitle({
			title:"订单评价"
		})
		this.orderid=ops.orderid;
		this.getPage();
	},
	methods:{
		getPage:function(){
			var that=this;
			uni.request({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=raty&ajax=1&orderid="+this.orderid,
				data:{
					authcode:that.app.getAuthCode()
				},
				success:function(rs){
					var res=rs.data;
					that.pageLoad=true; 
					that.pageData=res.data;
				}	
			})
		},
		 
		ratySubmit:function(e){
			var that=this;
			uni.request({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=ratysave&ajax=1&authcode="+that.app.getAuthCode(),
				data:e.detail.value,
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				success:function(rs){
					 
					that.getPage();
				}	
			})
		}
	}
}
